const CONSTANTS = {
    ADD_MESSAGE: 'messages/ADD_MESSAGE',
    UPDATE_NEW_MESSAGE_INFORMATION: 'messages/UPDATE_NEW_MESSAGE_INFORMATION',
    UPDATE_NEW_MESSAGE_ID: 'messages/UPDATE_NEW_MESSAGE_ID'
}

let initialState = {
    dialogsData: [
        {
            id: 1,
            name: "Nick",
            surname: "Kupreev",
            photoUrl: 'https://sun9-9.userapi.com/impg/UG8VExZpPRlb6YGYJEKAeyP-rkLkLhx_U6dsdw/zwWIQ9UZ118.jpg?size=960x1280&quality=96&sign=b4742913b9a925638ad8ebaac472a4e5&type=album',
            messages: [
                {
                    message: 'I love you',
                    date: '17.07.2021',
                    flag: 'incoming',
                },
                {
                    message: 'You are the best one',
                    date: '17.07.2021',
                    flag: 'incoming',
                },
                {
                    message: 'I love you too',
                    date: '17.07.2021',
                    flag: 'outgoing',
                },
            ],
        },
        {
            id: 2,
            name: "Denis",
            surname: "Baikhanov",
            photoUrl: 'https://sun9-27.userapi.com/impf/c837134/v837134870/2541e/lZAF8Vr9OhQ.jpg?size=720x599&quality=96&sign=7ef1de645bff24c67f90925a2194828d&type=album',
            messages: [
                {
                    message: 'I`m going home, sis',
                    date: '17.07.2021',
                    flag: 'incoming',
                },
            ]
        },
        {
            id: 3,
            name: 'Anna',
            surname: 'Baikhanova',
            messages: [
                {
                    message: 'How are you, baby?',
                    date: '17.07.2021',
                    flag: 'incoming',
                },
            ]
        },
    ],
};


const messagesReducer = (state = initialState, action) => {

    switch (action.type) {
        case CONSTANTS.ADD_MESSAGE: {
            return {
                ...state,
                dialogsData: state.dialogsData.map(dialog => {
                    if (dialog.id === action.id && action.message !== undefined && action.message !== '') {
                        return {...dialog, messages: [...dialog.messages, {message: action.message, flag: 'outgoing', date: new Date().toLocaleDateString()}]}
                    }
                    return dialog;
                }),
            }

        }
        default: return state;
    }
}

export const addMessage = (message, id) => ({type: CONSTANTS.ADD_MESSAGE, message, id});


export default messagesReducer;