import React from "react";

import stylesForm from '../../../Styles/FormsControl.module.css'

export const TextareaComponent = (props) => {
    const {input , meta , child , ...restProps} = props;
    return <FormElement {...props}><textarea {...input} {...restProps}/></FormElement>
}


export const InputComponent = (props) => {
    const {input , meta , child , ...restProps} = props;
    return <FormElement {...props}><input {...input} {...restProps}/></FormElement>
}


const FormElement = ({input, meta, ...props}) => {
    const hasError = meta.touched && meta.error

    return (
        <div className={stylesForm.formControl + ' ' + (hasError && !meta.active /*&& meta.submitting*/ ? stylesForm.error : '')}>
            {
                hasError && !meta.active /*&& meta.submitting*/ && <span className={stylesForm.error__message}>{meta.error}</span>
            }
            {props.children}
        </div>
    )
}
