import * as axios from "axios";

const toServer = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": '36f229d9-d430-4d11-b0e9-8490fb7cb01e',
    }
})

export const usersAPI = {
    async getUsers(currentDepth = 1, displayingUsersAmount = 10) {
        return (await toServer.get( `users?page=${ currentDepth }&count=${ displayingUsersAmount }`)).data
    },
}

export const profileAPI = {
    async getUserProfile(id) {
        return (await toServer.get(`profile/${id}`)).data
    },
    async getUserStatus(id){
        return (await toServer.get(`profile/status/${id}`)).data
    },
    async updateUserStatus(status) {
        return (await toServer.put(`profile/status/`, { status }))
    }
}

export const followingAPI = {
    async followToUser(id) {
        return (await toServer.post ( `follow/${ id }` )).data
    },

    async unfollowOfUser(id) {
        return (await toServer.delete ( `follow/${ id }` )).data
    }
}

export const authAPI = {
    async authorizeMe(){
        return (await toServer.get('auth/me')).data
    },
    async login(email, password, rememberMe){
        return (await toServer.post('auth/login', {email, password, rememberMe})).data
    },
    async logout(){
        return (await toServer.delete('auth/login')).data
    }
}

