import { followingAPI , usersAPI } from "../../Api/Api";

const CONSTANTS = {
    FOLLOW: 'search/FOLLOW',
    UNFOLLOW: 'search/UNFOLLOW',
    SET_USERS: 'search/SET_USERS',
    TOGGLE_IS_FETCHING: 'search/TOGGLE_IS_FETCHING',
    CHANGE_DEPTH: 'search/CHANGE_DEPTH',
    TOGGLE_IS_FOLLOWING: 'search/TOGGLE_IS_FOLLOWING'
}

let initialState = {
    users: [],
    displayingUsersAmount: 10,
    totalUsersCount: 0,
    currentDepth: 1,
    isFetching: false,
    followingInProgress: [],
}

const searchReducer = (state = initialState, action) => {

    switch (action.type) {
        case CONSTANTS.FOLLOW: {
            return {
                ...state ,
                users: state.users.map ( user => {
                    if (user.id === action.id) {
                        return {...user , followed: true}
                    }
                    return user;
                } )
            };
        }
        case CONSTANTS.UNFOLLOW: {
            return {
                ...state ,
                users: state.users.map ( user => {
                    if (user.id === action.id) {
                        return {...user , followed: false}
                    }
                    return user;
                } )
            };
        }

        case CONSTANTS.SET_USERS: {
            return{
                ...state,
                users: [...state.users, ...action.data.items],
                totalUsersCount: action.data.totalCount,
            }
        }
        case CONSTANTS.TOGGLE_IS_FETCHING: {
            return {...state, isFetching: action.isFetching}
        }
        case CONSTANTS.CHANGE_DEPTH: {
            return {...state, currentDepth: action.depth}
        }
        case CONSTANTS.TOGGLE_IS_FOLLOWING: {
            return {...state, followingInProgress: action.isFetching ? [...state.followingInProgress, action.userId] : state.followingInProgress.filter(id => id === action.userId)}
        }
        default: return state;
    }
}

export const follow = (id) => ({type: CONSTANTS.FOLLOW, id});
export const unfollow = (id) => ({type: CONSTANTS.UNFOLLOW, id});
export const setUsers = (data) => ({type: CONSTANTS.SET_USERS, data})
export const toggleIsFetching = (isFetching) => ({type: CONSTANTS.TOGGLE_IS_FETCHING, isFetching})
export const changeDepth = (depth) => ({type: CONSTANTS.CHANGE_DEPTH, depth})
export const toggleIsFollowing = (isFetching, id) => ({type: CONSTANTS.TOGGLE_IS_FOLLOWING, isFetching, id})

export const getUsers = (currentDepth, displayingUsersAmount) => async (dispatch) => {
    dispatch(toggleIsFetching ( true ));

    let data = await usersAPI.getUsers(currentDepth, displayingUsersAmount)

    dispatch(toggleIsFetching ( false ));
    dispatch(setUsers ( data ))
}

export const downloadUsers = (currentDepth, displayingUsersAmount ) => async (dispatch) => {
    dispatch(changeDepth ( currentDepth + 1 ));

    dispatch(toggleIsFetching ( true ));

    let data = await usersAPI.getUsers(currentDepth + 1, displayingUsersAmount)

    dispatch(toggleIsFetching ( false ));
    dispatch(setUsers ( data ))
}

export const unfollowing = ( id ) => async (dispatch) => {
    await followUnfollowFlow ( dispatch , id , followingAPI.unfollowOfUser , unfollow )
}

export const following = ( id ) => async (dispatch) => {
    await followUnfollowFlow ( dispatch , id , followingAPI.followToUser , follow )
}


const followUnfollowFlow = async (dispatch, id, apiMethod, AC) => {
    dispatch(toggleIsFollowing(true, id))

    const data = await apiMethod(id)

    if(!data.resultCode){
        dispatch(AC(id))
    }

    dispatch(toggleIsFollowing(false, id))
}

export default searchReducer;