import { profileAPI } from "../../Api/Api";

const CONSTANTS = {
    ADD_POST: 'profile/ADD_POST',
    DELETE_POST: 'profile/DELETE_POST',
    UPDATE_NEW_POST_INFORMATION: 'profile/UPDATE_NEW_POST_INFORMATION',
    SET_NEW_USER: 'profile/SET_NEW_USER',
    UPDATE_USER_STATUS: 'profile/UPDATE_USER_STATUS',
    IS_MY_PROFILE: 'profile/IS_MY_PROFILE'
}

let initialState = {
    profile: null,
    status: '',
    isMyProfile: null,
    postsData: [
        {
            text: 'I love React',
            date: '13.07.2021'
        },
        {
            text: 'I want to job!',
            date: '13.07.2021'
        },
        {
            text: 'How are you?',
            date: '15.07.2021'
        },
        {
            text: 'Wtf..?',
            date: '15.07.2021'
        },
    ],
};

const profileReducer = (state = initialState, action) => {

    let stateCopy = Object.assign({}, state);

    switch (action.type){
        case CONSTANTS.ADD_POST: {
            return {
                ...state,
                postsData: [...state.postsData, {text: action.text, date: action.date}],
            }
        }
        case CONSTANTS.DELETE_POST: {
            return {
                ...state,
                postsData: state.postsData.filter(post => post)
            }
        }
        case CONSTANTS.SET_NEW_USER: {
            return {
                ...state,
                profile: action.user,
            }
        }
        case CONSTANTS.UPDATE_USER_STATUS: {
            return {
                ...state,
                status: action.status,
            }
        }
        case CONSTANTS.IS_MY_PROFILE: {
            return {
                ...state,
                isMyProfile: action.isMyProfile,
            }
        }
        default: return stateCopy;
    }
}


export const addPost = (text) => ({type: CONSTANTS.ADD_POST, text, date: new Date().toLocaleDateString()});
export const deletePost = (postId) => ({type: CONSTANTS.DELETE_POST, postId});

export const setNewUser = (user) => ({type: CONSTANTS.SET_NEW_USER, user})
export const updateUserStatus = (status) => ({type: CONSTANTS.UPDATE_USER_STATUS, status})
export const setMyProfileField = (isMyProfile) => ({type: CONSTANTS.IS_MY_PROFILE, isMyProfile})



export const getProfile = (id) => async (dispatch) => {
    let data = await profileAPI.getUserProfile(id)

    dispatch(setNewUser(data))
}

export const getUserStatus = (id) => async (dispatch) => {
    let data = await profileAPI.getUserStatus(id)
    dispatch(updateUserStatus(data))
}

export const putUserStatus = (newStatus) => async (dispatch) => {
    let response = await profileAPI.updateUserStatus(newStatus)

    if(!response.resultCode){
        dispatch(updateUserStatus(newStatus))
    }
}

export default profileReducer;