import React from 'react';

import stylesApp from './Styles/Page.module.css';

import Page from "./Components/Main/Page";
import HeaderContainer from "./Components/Header/HeaderContainer.jsx";
import Preloader from "./Components/Сommon/Preloader/Preloader";

import { connect } from "react-redux";
import { authorize } from "./Redux/Reducers/authReducer";
import { initializeApp } from "./Redux/Reducers/appReducer";




class App extends React.Component{
    componentDidMount() {
        this.props.initializeApp()
    }

    render() {
        if(!this.props.initialized) return <Preloader/>

        return(
            <div className={stylesApp.wrapper}>
                <HeaderContainer/>
                <Page/>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        initialized: state.app.initialized
    }
}


export default connect(mapStateToProps, {authorize, initializeApp})(App);
