import { authorize } from "./authReducer";

const CONSTANTS = {
    SET_INITIALIZED: 'app/SET_INITIALIZED'
}

let initialState = {
    initialized: false
}

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case CONSTANTS.SET_INITIALIZED: {
            return {
                ...state,
                initialized: action.isInitialized
            }
        }
        default: return state;
    }
}

export const isInit = (isInitialized) => ({ type: CONSTANTS.SET_INITIALIZED, isInitialized })

export const initializeApp = () => (dispatch) => {
    dispatch(authorize()).then(() =>  dispatch(isInit(true)))
 }

export default appReducer;