import { authAPI , profileAPI } from "../../Api/Api";
import { stopSubmit } from "redux-form";
import { setNewUser } from "./profileReducer";

const CONSTANTS = {
    SET_AUTH_DATA: 'auth/SET_AUTH_DATA',
    SET_USER_PROFILE: 'auth/SET_USER_PROFILE',
    TOGGLE_IS_FETCHING: 'auth/TOGGLE_IS_FETCHING',
    AUTHORIZING: 'auth/AUTHORIZING'
}

let initialState = {
    currentProfile: null,
    userId: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case CONSTANTS.SET_AUTH_DATA: {
            return {
                ...state,
                ...action.data,
                isAuth: true,
            }
        }
        case CONSTANTS.SET_USER_PROFILE: {
            return{
                ...state,
                currentProfile: action.profileData
            }
        }
        case CONSTANTS.TOGGLE_IS_FETCHING: {
            return{
                ...state,
                isFetching: action.isFetching
            }
        }
        case CONSTANTS.AUTHORIZING: {
            return{
                ...state,
                isAuth: action.isAuth
            }
        }
        default: return state;
    }
}

export const setAuthData = (userId, email, login) => ({type: CONSTANTS.SET_AUTH_DATA, data: {userId , email , login}})
export const setUserProfile = (profileData) => ({type: CONSTANTS.SET_USER_PROFILE, profileData})
export const toggleIsFetching = (isFetching) => ({type: CONSTANTS.TOGGLE_IS_FETCHING, isFetching})
export const authorizing = (isAuth) => ({type: CONSTANTS.AUTHORIZING, isAuth})

export const authorize = () => async ( dispatch ) => {
    dispatch(toggleIsFetching(true));

    const data = await authAPI.authorizeMe()

    if(!data.resultCode) {
        dispatch(setAuthData ( data.data.id , data.data.email , data.data.login ))

        profileAPI.getUserProfile(data.data.id)
            .then ( data => {
                dispatch(setUserProfile ( data ))
                dispatch(setNewUser(data))
            } )

        dispatch(toggleIsFetching ( false ));
    }
}

export const login = (email, password, rememberMe) => async (dispatch) => {
    dispatch(toggleIsFetching(true));

    const data = await authAPI.login(email, password, rememberMe)

    if(!data.resultCode) {
        dispatch(authorizing(true))

        authAPI.authorizeMe()
            .then(data => {
                dispatch(setAuthData ( data.data.id , data.data.email , data.data.login ))
            })

        profileAPI.getUserProfile(data.data.userId)
            .then ( data => {
                dispatch(setUserProfile ( data ))
            } )
    }else{
        dispatch(stopSubmit("loginForm", {_error: data.messages[0]}))
    }

    dispatch(toggleIsFetching ( false ));
}

export const logout = () => async (dispatch) => {
    dispatch(toggleIsFetching(true));

    const data = authAPI.logout()

    if(!data.resultCode) {
        dispatch(authorizing(false))
    }

    dispatch(toggleIsFetching ( false ));
}

export default authReducer;