import React from 'react'

import stylesHeader from '../../Styles/Header.module.css'
import stylesGeneral from '../../Styles/Page.module.css'

import siteLogo from '../../assets/images/siteLogo.jpg'

import { NavLink } from "react-router-dom";

import avatar from '../../assets/images/avatar.jpg'
import logoutImage from '../../assets/images/logout.png'


const Header = (props) => {

    let loginElem = <div className={stylesHeader.header__login + ' ' + stylesHeader.login}>
        <NavLink to={'/login'}>Login</NavLink>
    </div>

    let userElem = <div className={stylesHeader.header__user + ' ' + stylesHeader.user}>
        <div className={stylesHeader.user__body}>
            <NavLink to={`/profile/${props.id}`}>{ props.login }</NavLink>
            <div className={stylesHeader.header__avatar}>
                <img src={props.url || avatar} alt=""/>
            </div>
            <div className={stylesHeader.header__logout} >
                <input type={ "image" } src={ logoutImage } onClick={ props.logout }/>
            </div>
        </div>
    </div>

    return (
        <div className={stylesHeader.header}>
            <div className={`${stylesHeader.header__container} ${stylesGeneral._container}`}>
                <div className={stylesHeader.header__body}>
                    <div className={stylesHeader.header__logo}>
                        <img src={siteLogo} alt="Site logo"/>
                    </div>
                    <div className={stylesHeader.header__actions}>
                        {props.isAuth ? userElem : loginElem}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header;